import api from '../../services/api';

export interface IGitHubRateLimits {
  limit: number;
  remaining: number;
  reset: number;
}

export interface IGitHubUser {
  id?: number;
  login?: string;
  name?: string;
  type?: string;
  avatar_url?: string;
  html_url?: string;
  following?: number;
  followers?: number;
  public_repos?: number;
}

export default class GitHub {

  static followingList: IGitHubUser[] = [];
  static followersList: IGitHubUser[] = [];

  static async getRateLimits() {

    return await api.get('rate_limit')
      .then( (response) => {
        return response.data.resources.core as IGitHubRateLimits;
      })
      .catch( (error) => {
        throw error;
      });

  }

  static async getUser(username: string) {

    return await api.get(`users/${ username }`)
      .then( (response) => {
        return response.data as IGitHubUser;
      })
      .catch( (error) => {
        throw error;
      });
  }

  static async getFollowing(username: string, page: number) {

    try {
      let response = await api.get(`users/${ username }/following?per_page=100&page=${ page }`)
      this.followingList = [...this.followingList, ...response.data];
      
      if (response.data.length === 100)
        await this.getFollowing(username, ++page);

      return this.followingList;
    } catch (error) {
      throw error;
    }
    
  }

  static async getFollowers(username: string, page: number) {
    
    try {
      let response = await api.get(`users/${ username }/followers?per_page=100&page=${ page }`)
      this.followersList = [...this.followersList, ...response.data];
      
      if (response.data.length === 100)
        await this.getFollowers(username, ++page);

      return this.followersList;
    } catch (error) {
      throw error;
    }

  }

  static getNotFollowers(following: IGitHubUser[], followers: IGitHubUser[]) {

    function comparer(otherArray: IGitHubUser[]){
      return function(current: IGitHubUser){
        return otherArray.filter(function(other){
          return other.id === current.id;
        }).length === 0;
      }
    }
    
    return following.filter(comparer(followers)) as IGitHubUser[];
  }

} 