import React from 'react';
import { Toast } from 'react-bootstrap';

import logoImg from '../../assets/images/github-logo.svg';

interface IGitHubToast {
  subtitle: string;
  subtitleStyle: string;
  show: boolean;
  toggleFunction: () => void;
}

const GitHubToast: React.FunctionComponent<IGitHubToast> = ({ subtitle, subtitleStyle, show, toggleFunction, children }) => {
  return (
    <Toast show={ show } onClose={ toggleFunction }>
      <Toast.Header>
        <img
          src={ logoImg }
          className="rounded mr-2 toast-image"
          alt=""
        />
        <strong className="mr-auto">GitHub</strong>
        <small className={ subtitleStyle }>{ subtitle }</small>
      </Toast.Header>
      <Toast.Body>{ children }</Toast.Body>
    </Toast>
  );
}

export default GitHubToast;